.home-page-wrapper{
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    padding: 80px 20px 40px;
    text-align: center;
    background-image: url(../images/bg-intro-mobile.png);
    background-color: hsl(0, 100%, 74%);
    .lern-to-code{
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 50px;
        color: hsl(0, 6%, 90%);
        .title{
            margin:0 30px 20px; 
            font-size:22px;
            font-weight: 700;
            color: #fff;
        }
    }
    .sign-up-form-wrapper{
        display: flex;
        flex-wrap: wrap;
        max-width: 650px;
        .free-try-btn{
            margin: 0 auto 20px;
            padding: 20px 40px;
            border: none;
            border-radius: 6px;
            cursor: pointer;
            box-shadow: 0 8px 5px 0px rgba(10, 10, 10, 0.3);
            background-color: hsl(248, 32%, 49%);
            color: #fff;
        }
        .form{
            padding: 20px;
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            background-color: #fff;
            border-radius: 6px;
            box-shadow: 0 8px 5px 0px rgba(10, 10, 10, 0.3);
            input{
                margin: 0 auto 10px auto;
                padding: 12px 10px;
                flex-basis: 95%;
                border-radius: 6px;
                border: 1px solid rgb(221, 221, 221);
            }
          
            .submit-btn{
                margin: 0 auto 15px;
                padding: 15px 30px;
                display: block;
                flex-basis: 95%;
                border: none;
                border-radius: 6px;
                cursor: pointer;
                text-transform: uppercase;
                transition: .3s;
                background-color:hsl(154, 59%, 51%);
                color: #fff;
                &:hover{
                    background-color:hsl(154, 79%, 59%);
                }
            }
            .agreement{
                padding: 0 20px;
                font-size: 12px;
                color: hsl(246, 25%, 77%);
                .terms{
                    color: hsl(0, 100%, 74%);
                }
            }
            p.required{
                margin:0 10px 10px 0;
                flex-basis: 100%;
                text-align: right;
                font-size: 12px;
                color: hsl(0, 100%, 74%);
            }
        }
    }
    footer{
        flex-basis: 100%;
    }
}

@media(min-width:481px){
    .home-page-wrapper{
        padding: 100px 80px 40px;
        .lern-to-code {
            padding: 0;
            justify-content: center;
            .tutotrials{
                padding-bottom:20px;
            }
        }
        .sign-up-form-wrapper{
            .free-try-btn{
                margin: 0 auto 30px;
                padding: 10px 40px;
                flex-basis: 85%;
            }
            .form{
                margin: auto;
                flex-basis: 85%;
            }
        }
       
    }
}

@media(min-width:769px){
    .home-page-wrapper{
        max-height: 0;
        padding: 220px 40px 250px;
        .lern-to-code{
            text-align: left;
            flex-basis: 50%;
            .title{
                margin: 130px 0 0;
                font-size: 35px;
                line-height: 1.2;
            }
            .tutotrials{
                padding: 0 ;
            }
        }
        .sign-up-form-wrapper{
            justify-content: flex-end;
            flex-basis: 50%;
            .free-try-btn{
                margin-bottom: 0;
            }
        }            
       
    }
}
@media(min-width:1200px){
    .home-page-wrapper{
        padding: 180px 100px 20px;
        .lern-to-code{
            justify-content: flex-start;
            .title{
                margin:180px 0px 0px; 

            }
        }
    }
}
@media(min-width:1400px){
    .home-page-wrapper{
        .lern-to-code{
            justify-content: flex-start;
            .title{
                margin:160px 0px 0px; 
            }
        }
    }
}